.cards-filter {
	font-size: 16px;
	padding: 10px 0;
	display: flex;
	gap: 40px;

	@media (min-width: 992px) {
		padding: 24px 0;
	}
}

.cards-filter__mobile-element {
	padding: 6px 20px;
	background-color: var(--primary-color);
	border-radius: 20px;
	color: #fff;
}

.sorting-elements,
.viewing-elements {
	display: flex;
	gap: 10px;
}

.sorting,
.viewing {
	display: flex;

	* {
		margin-bottom: 0;
	}
}

.sorting__name {
	white-space: nowrap;
}

.viewing__value {
	white-space: nowrap;
}

.sorting__item {
	margin: 0 10px;

	span {
		color: var(--recommend-link-color);
		font-weight: 500;
		cursor: pointer;
		user-select: none;
		display: none;

		&.current {
			display: block;
		}
	}
}

.sorting__icon {
	position: relative;
	padding-right: 20px;

	&::before {
	  position: absolute;
	  content: '';
	  top: calc(50% - 8px);
	  right: 0;
	  width: 15px;
	  height: 15px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}

	&--min {
		&::before {
			background-image: url('../img/icons/down-arrow.svg');
			transform: rotate(180deg);
		}
	}

	&--max {
		&::before {
			background-image: url('../img/icons/down-arrow.svg');
		}
	}
}

.viewing__item {
	margin: 0 10px;
	display: flex;
	gap: 20px;

	span {
		color: #909090;
		font-weight: 500;
		cursor: pointer;
		user-select: none;

		&.current {
			color: var(--recommend-link-color);
		}
	}
}