.sidebar-filter {
	padding: 20px 0 20px;

	.form-check {
		margin-bottom: 10px;
	}

	.form-check-label {
		font-weight: 500;
		line-height: 1.2;
		cursor: pointer;
		user-select: none;
	}

	.form-check-input {
		position: relative;
		top: -4px;
		cursor: pointer;

	}

	.form-control {
		border: var(--bs-border-width) solid var(--bs-border-color);
		border-radius: 8px;
		min-height: 10px;
		padding: 5px 10px;
	}

	.form-check-input,
	.form-control {
		box-shadow: 0 0 0 0;
	}
}

.sidebar-filter__group {
	margin-top: 20px;
	font-weight: 600;

	.sidebar-filter__group-title {
		margin-bottom: 0;
	}

	.form-check-label {
		font-weight: 300;
	}

	.form-check {
		margin-bottom: 0;
	}

	.sidebar-filter__flex {
		display: flex;
		flex-wrap: nowrap;
		gap: 10px;
	}
}

.sidebar-filter__buttons {
	margin-top: 10px;
	display: flex;
	flex-wrap: wrap;
	gap: 10px;

	* {
		border-radius: 8px;
		flex-grow: 1;
		width: calc(100% / 2 - 5px);
	}

	.close-button {
		border: 1px solid var(--primary-color);
		background-color: transparent;
		color: var(--primary-color);
	}
}