.product-card {
	font-size: 16px;

	.swiper {
		width: 100%;
		height: 300px;
		margin-left: auto;
		margin-right: auto;

		a {
			max-height: 300px;
			width: 100%;

			img {
				max-height: 300px;
				object-position: center;
			}
		}
	}

	.productСardSlider {
		height: 80%;
		max-height: 300px;
		width: 100%;

		@media (min-width: 992px) {
			height: 80%;
		}
	}

	.productСardSliderThumbs {
		height: 20%;
		border-radius: 16px;
		overflow: hidden;

		.swiper-slide {
			width: 25%;
			height: 100%;
			opacity: 0.4;
			transition: all .2s ease;
		}

		.swiper-slide-thumb-active {
			opacity: 1;
		}
	}

	.swiper-slide {
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		background-size: cover;
		background-position: center;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 16px;
			user-select: none;
		}
	}
}

.product-card__wrap {
	display: flex;
	flex-wrap: nowrap;
	gap: 10px;
	width: 100%;
	flex-direction: column;

	@media (min-width: 992px) {
		display: flex;
		flex-wrap: nowrap;
		gap: 15px;
		flex-direction: row;
	}

	@media (min-width: 1200px) {
		gap: 20px;
	}
}

.product-card__profile {
	width: 100%;
	order: 2;

	@media (min-width: 992px) {
		width: 36%;
		order: 1;
	}

	@media (min-width: 1200px) {
		width: 38%;
	}

	@media (min-width: 1400px) {
		width: 35%;
	}
}

.product-card__profile-info {
	display: flex;
	flex-wrap: wrap;
	row-gap: 10px;
	column-gap: 10px;
	align-items: center;
	margin-top: 10px;
	justify-content: center;

	@media (min-width: 576px) {
		row-gap: 0;
		column-gap: 10px;
	}

	@media (min-width: 992px) {
		gap: 0;
		flex-direction: column;
		margin-top: 0;
		justify-content: flex-start;
		align-items: flex-start;
	}
}

.product-card__profile-slider {
	border-radius: 16px;
	overflow: hidden;
	position: relative;
}

.product-card__profile-icons {
	display: flex;
	width: 100%;
	height: 38px;
	position: absolute;
	bottom: 0;
	justify-content: flex-end;
	padding-right: 5px;

	* {
		position: relative;
		z-index: 2;
	}

	.message-icon {
		transition: all .2s ease;

		&:hover {
			transform: translateY(-2px);
		}
	}
}

.message-icon {
	width: 35px;
	height: 35px;
	background-image: url('../img/icons/message.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 21px;
	z-index: 2;
	filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.3));
}

.favorite-icon {
	width: 35px;
	height: 35px;
	background-image: url('../img/icons/heart.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 20px;
	z-index: 2;
	filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.3));
	cursor: pointer;

	&.active {
		background-image: url('../img/icons/heart-filled.svg');
		filter: none;
	}
}

.product-card__profile-title {
	line-height: 1.2;
	font-weight: 300;
	font-size: 16px;
	margin-bottom: 0;

	@media (min-width: 992px) {
		margin-top: 10px;
	}

	@media (min-width: 1200px) {
		margin-top: 15px;
	}

	@media (min-width: 1400px) {
		margin-top: 10px;
	}
}

.product-card__profile-rating {
	display: flex;
	flex-wrap: nowrap;
	gap: 6px;
	position: relative;
	top: -1px;
}

.product-card__profile-rating-value {
	font-weight: 500;
	font-size: 20px;
	position: relative;
	top: 1px;
}

.product-card__profile-rating-reviews {
	font-size: 16px;
	line-height: 1.2;
	font-weight: 300;

	&:hover {
		color: var(--primary-color);
	}
}

.product-card__profile-phone-btn,
.product-card__profile-phone-show {
	padding: 15px 10px;
	min-height: auto;
	width: 100%;
	margin-top: 10px;

	@media (min-width: 576px) {
		padding: 7px 10px;
	}

	@media (min-width: 992px) {
		font-size: 15px;
		margin-top: 15px;
	}

	@media (min-width: 1200px) {
		margin-top: 15px;
	}

	@media (min-width: 1400px) {
		font-size: 16px;
		margin-top: 10px;
	}
}

.product-card__profile-phone-btn {
	background-color: var(--bs-success-color);
	//color: var(--primary-color);

	&:hover {
		background-color: var(--bs-success-color-hover) !important;
	}
}

.product-card__profile-phone {
	display: block;
	color: #000;
	text-decoration: none;
	font-weight: 500;

	&:hover {
		color: var(--primary-color);
	}
}

.product-card__info {
	display: flex;
	flex-direction: column;
	order: 1;

	@media (min-width: 992px) {
		order: 2;
	}
}

.product-card__title {
	display: block;
	font-weight: 700;
	font-size: 18px;
	color: var(--primary-color);
	text-decoration: none;
	line-height: 1.2;
	margin-bottom: 12px;
	transition: all .2s ease;

	&:hover {
		color: var(--primary-color);
		text-decoration: underline;
	}

	@media (min-width: 768px) {
		margin-bottom: 22px;
	}

	@media (min-width: 1200px) {
		margin-bottom: 10px;
	}

	@media (min-width: 1400px) {
		margin-bottom: 22px;
	}
}

.product-card__prices {
	margin-bottom: 12px;

	@media (min-width: 768px) {
		margin-bottom: 20px;
	}
}

.product-card__price-item {
	font-weight: 300;
	font-size: 16px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	&:not(:last-child) {
		margin-bottom: 8px;
	}

	* {
		margin-bottom: 0;
		line-height: 1;
		display: inline-flex;
	}

	span {
		font-weight: 700;
	}
}

.product-card__price-divider {
	border-bottom: 1px dotted #000;
	flex-grow: 1;
	margin: 0 5px;
	position: relative;
	line-height: 1;
	bottom: 3px;
	opacity: .5;
}

.product-card__text {
	font-size: 16px;
	font-weight: 400;
	line-height: 1.3;
	@include line-clamp(5);

	@media (min-width: 576px) {
		@include line-clamp(4);
	}

	@media (min-width: 992px) {
		@include line-clamp(5);
	}

	@media (min-width: 1200px) {
		@include line-clamp(5);
	}

	@media (min-width: 1400px) {
		@include line-clamp(6);
	}

	* {
		&:last-child {
			margin-bottom: 0;
		}
	}


}

.product-card__city {
	font-style: italic;
	font-size: 16px;
	margin-top: 4px;
	margin-bottom: 0;

	@media (min-width: 992px) {
		margin-top: 10px;
	}

	@media (min-width: 1200px) {
		margin-top: 8px;
	}

	@media (min-width: 1400px) {
		margin-top: 12px;
	}
}

.product-card__thumbs {
	margin-top: 10px;

	@media (min-width: 992px) {
		margin-top: 12px;
	}

	@media (min-width: 1200px) {
		margin-top: 15px;
	}

	@media (min-width: 1400px) {
		margin-top: 20px;
	}
}