.product-content {
	padding: 0;

	@media (max-width: 1199px) {
		overflow: hidden;
	}

	@media (min-width: 1200px) {
		padding-top: 30px;
	}

	.main-hero__form {
		max-width: unset;
	}

	.announcement-map__wrap {
		margin-top: 20px;
		width: 100%;
		max-width: 100%;
		padding: 0;

		@media (min-width: 768px) {
			margin-top: 0;
		}
	}
}

.product-content__wrap {
	display: flex;
	gap: 40px;
}

.product-content__sidebar-temp {
	margin-bottom: 15px;

	@media (min-width: 1200px) {
		margin-bottom: 0;
		overflow-y: scroll;
		max-height: calc(100vh - 100px);
		position: sticky;
		top: calc(72px + 30px);

		&.sticky-bottom {
			display: flex;

			.product-content__sidebar {
				position: sticky;
				top: auto;
				bottom: 0;
				//align-self: flex-end;
				margin-top: auto;
			}
		}
	}
}

/* Скрываем scrollbar для Chrome, Safari и Opera */
.product-content__sidebar-temp::-webkit-scrollbar {
	display: none;
}

/* Скрываем scrollbar для IE, Edge и Firefox */
.product-content__sidebar-temp {
	-ms-overflow-style: none;  /* IE и Edge */
	scrollbar-width: none;  /* Firefox */
}

.product-content__col-sidebar {
	@media (min-width: 1400px) {
		width: 22%;
	}
}

.product-content__col-content {
	@media (min-width: 1400px) {
		width: 78%;
	}
}



.product-sidebar__filter {
	@media (max-width: 1199px) {
		height: calc(100dvh - 72px);
		overflow-y: scroll;
		display: flex;
		flex-direction: column;
		overflow-x: hidden;
		padding-right: calc(var(--bs-gutter-x)* .5);
		padding-left: calc(var(--bs-gutter-x)* .5);
	}
}


.mobile-modal-element {
	@media (max-width: 1199px) {
		position: fixed;
		top: calc(60px);
		left: 0;
		background: var(--bs-body-bg);
		width: 100%;
		height: 100%;
		z-index: 5;
		opacity: 0;
		transition: all .3s ease;
		pointer-events: none;
		transform: translateX(-100%);
		overflow: hidden;
		touch-action: auto;

		.menu-open & {
			opacity: 1;
			pointer-events: auto;
			transition: all .3s ease;
			transform: translateX(0);
		}

		&__container {
			height: calc(100dvh - 72px);
			overflow-y: scroll;
			display: flex;
			flex-direction: column;
			overflow-x: hidden;
		}
	}
}



html.menu-open {
	position: relative;
	touch-action: none;
	overflow: hidden;
}







.product-content__sidebar {

}

.product-sidebar__services {
	margin-top: auto;
}

.product-content__content {
}

.product-sidebar__service-list {
	margin-bottom: 5px;

	@media (min-width: 992px) {
		margin-bottom: 10px;
	}

	li {
		line-height: 1.4;
		font-size: 16px;

		@media (max-width: 1199px) {
			position: relative;
			padding-left: 10px;
			display: inline;
		}

		&::before {


			@media (max-width: 1199px) {
				position: absolute;
				content: "";
				top: 8px;
				left: -3px;
				width: 4px;
				height: 4px;
				background-color: #313131;
				border-radius: 50%;
			}
		}

		&:first-child {
			padding-left: 0;

			&::before {
				content: none;
			}
		}

		&:not(:last-child) {
			margin-right: 10px;

			@media (min-width: 1200px) {
				margin-bottom: 10px;
			}
		}

		&:last-child {
		}
	}

	a {
		text-decoration: none;
		color: #000;

		&:hover {
			color: var(--primary-color-hover);
		}
	}
}

.product-sidebar__service-more {
	font-weight: 600;
	text-decoration: none;

	&:hover {
		text-decoration: underline;
		color: var(--primary-color);
	}
}

.product-content__cards-list {
	display: flex;
	row-gap: 50px;
	column-gap: 50px;
	flex-wrap: wrap;

	@media (min-width: 576px) {
		row-gap: 50px;
		column-gap: 20px;
	}

	@media (min-width: 992px) {
		row-gap: 75px;
		column-gap: 50px;
	}

	@media (min-width: 1200px) {
		row-gap: 100px;
		column-gap: 50px;
	}

	&.table-view {


	}

	&.card-view {
		row-gap: 100px;
		column-gap: 50px;

		.product-card__wrap {
			gap: 40px;
		}

		.product-content__cards-item {
			width: 100%;
			position: relative;
			min-height: 360px;
		}

		.product-card__profile {
			width: 20%;
		}

		.product-card__info {
			flex-grow: 1;
		}

		.product-card__text {
			@include line-clamp(3);
		}

		.product-card__city {
			margin-top: 5px;
		}

		.product-card__thumbs {
			position: absolute;
			bottom: 0;
			right: 0;
			width: calc(80% - 40px);
			margin-top: 30px;

			.swiper-slide {
				img {
					max-height: 150px;
				}
			}
		}
	}
}

.product-content__cards {
	margin: 20px 0 0;
}

.product-content__cards-filter {
	margin-bottom: 20px;
	align-items: center;

	@media (max-width: 1199px) {
		overflow-x: scroll;
	}
}

.product-content__cards-item {
	width: 100%;

	@media (min-width: 576px) {
		width: calc(100% / 2 - 10px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 2 - 25px);
	}
}