.main-hero {
	& + .services {
		margin-top: -130px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			margin-bottom: 20px;
		}

		@media (min-width: 768px) {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			margin-top: -175px;
		}
	}

	& + .announcement-map {
		margin-top: -130px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			margin-bottom: 20px;
		}

		@media (min-width: 768px) {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			margin-top: -175px;
		}
	}

	&--with-image {

		.main-hero__plate {
			border-radius: 0;
			position: relative;
			padding: 30px 25px 90px;
			background-position: center right -75px;

			@media (min-width: 768px) {
				padding: 30px 25px 95px;
				background-position: center;
				background-size: 140%;
			}

			@media (min-width: 992px) {
				padding: 50px 25px 180px;
			}

			@media (min-width: 1200px) {
				padding: 100px 40px 140px;
				background-size: 120%;
			}

			& > * {
				position: relative;
				z-index: 2;
			}

			&::after {
			  position: absolute;
			  content: '';
			  bottom: 0;
			  right: 0;
			  width: 100%;
			  height: calc(100% - 30px);
				background-image: url('../img/bg/mian-hero-image-full.png');
				background-repeat: no-repeat;
				background-position: bottom -104px left calc(50% + 122px);
				background-size: calc(150px + 20vw);

				@media (min-width: 576px) {
					background-position: bottom -104px left calc(50% + 225px);
					background-size: calc(150px + 26vw);
				}

				@media (min-width: 768px) {
					background-size: 338px;
					background-position: bottom -194px left calc(50% + 172px);
					transform: scale(-1, 1);
				}

				@media (min-width: 992px) {
					transform: scale(1);
					background-position: bottom -146px left calc(50% + 227px);
					background-size: 434px;
					top: 0;
					height: 100%;
				}

				@media (min-width: 1200px) {
					background-size: 687px;
					background-position: bottom -431px right calc(50% - 357px);
				}

				@media (min-width: 1400px) {
					background-position: bottom -431px right calc(50% - 400px);
				}
			}
		}

		.main-hero__form {
			@media (max-width: 575px) {
				max-width: 230px;
			}

			& > * {
				@media (min-width: 768px) {
					width: calc(50% - 12px);
				}

				&:nth-child(1) {
					@media (min-width: 992px) {
						width: calc(36% - 24px);
					}

					@media (min-width: 1200px) {
						width: calc(38% - 24px);
					}

					@media (min-width: 1400px) {
						width: 415px;
					}
				}

				&:nth-child(2) {
					@media (min-width: 992px) {
						width: calc(30% - 24px);
					}

					@media (min-width: 1200px) {
						width: calc(28% - 24px);
					}

					@media (min-width: 1400px) {
						width: 297px;
					}
				}

				&:nth-child(3) {
					@media (min-width: 992px) {
						width: calc(35% - 24px);
					}

					@media (min-width: 1200px) {
						width: calc(28% - 24px);
					}

					@media (min-width: 1400px) {
						width: 317px;
					}
				}
			}
		}

		& + .services {
			margin-top: -45px;
			margin-bottom: 10px;

			@media (min-width: 576px) {
				margin-top: -65px;
			}

			@media (min-width: 768px) {
				margin-bottom: 0;
			}

			@media (min-width: 992px) {
				margin-top: -72px;
			}
		}
	}

	&--inner {
		.section-title--main {
			@media (min-width: 360px) {
				max-width: 222px;
				font-size: 22px;
			}

			@media (min-width: 576px) {
				max-width: 60%;
				font-size: 25px;
			}

			@media (min-width: 768px) {
				max-width: 55%;
			}
		}

		.main-hero__plate {
			padding: 25px 15px 170px;

			@media (min-width: 576px) {
				padding: 25px 15px 200px;
			}

			@media (min-width: 768px) {
				padding: 25px 15px 160px;
			}

			@media (min-width: 992px) {
				padding: 90px 40px 230px;
			}

			@media (min-width: 1200px) {
				padding: 90px 40px 260px;
			}
		}

		.main-hero__form {
			max-width: 230px;

			@media (min-width: 576px) {
				max-width: 60%;
			}

			@media (min-width: 768px) {
				max-width: unset;
			}

			@media (min-width: 1200px) {
			}
		}
	}

	&--request {
		.main-hero__plate {
			background-size: cover;

			@media (min-width: 992px) {
				padding-bottom: 60px;
			}

			@media (min-width: 1200px) {
				padding-bottom: 100px;
			}

			&::after {
				background-position: bottom -151px left calc(50% + 114px);
				background-size: 338px;

				@media (min-width: 768px) {
					transform: scale(1, 1);
				}

				@media (min-width: 992px) {
					transform: scale(-1, 1);
					background-position: bottom -77px left calc(50% + 231px);
					background-size: 358px;
				}

				@media (min-width: 1200px) {
					transform: scale(1);
					background-size: 565px;
					background-position: bottom 65px left calc(50% + 418px);
				}

				@media (min-width: 1400px) {
					background-position: bottom 65px left calc(50% + 460px);
				}
			}
		}

		form {
			display: flex;
			flex-direction: column;
			gap: 20px;
			margin-bottom: 110px;

			@media (min-width: 576px) {
				flex-direction: row;
				flex-wrap: wrap;
			}

			@media (min-width: 992px) {
				row-gap: 40px;
				column-gap: 20px;
			}

			@media (min-width: 1200px) {
				margin-bottom: 0;
				max-width: 60%;
			}

			@media (min-width: 1400px) {
				max-width: 60%;
				margin-bottom: 0;
			}
		}

		.request__item {
			position: relative;
			flex-grow: 1;
			width: 100%;
			height: fit-content;

			@media (min-width: 576px) {
				&:nth-child(1) {
					width: calc(50% - 20px);
				}

				&:nth-child(2) {
					width: calc(50% - 20px);
				}

				&:nth-child(3) {
					width: calc(50% - 20px);
				}

				&:nth-child(4) {
					width: calc(50% - 20px);
				}
			}

			@media (min-width: 992px) {
				&:not(:last-child) {
					width: calc(100% / 3 - 20px) !important;
				}

				&:last-child {
					display: flex;
					justify-content: flex-end;
				}
			}

			@media (min-width: 1200px) {
				&:not(:last-child) {
					width: calc(100% / 2 - 20px) !important;
				}

				&:last-child {
					justify-content: flex-start;
				}
			}

			.valid-feedback,
			.invalid-feedback {
				position: absolute;
				right: 15px;
				bottom: -18px;
				text-align: right;
			}
		}
	}
}

.main-hero__plate {
	position: relative;
	background-repeat: no-repeat;
	background-position: center right -20px;
	background-size: cover;
	padding: 25px 15px 180px;
	border-radius: 20px;
	background-image: url('../img/bg/mian-hero.svg');

	@media (min-width: 768px) {
		background-position: center;
	}

	@media (min-width: 768px) and (max-width: 991px) {
		padding-bottom: 160px;
	}

	@media (min-width: 992px) {
		padding: 90px 40px 220px;
	}

	@media (min-width: 1200px) {
		border-radius: 40px;
	}

	& > .container {
		position: relative;
		z-index: 2;
	}
}

.main-hero__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center right;
	border-radius: 20px;
	opacity: 0;

	@media (min-width: 360px) {
		background-size: clamp(327px, 83vw, 429px);
		background-position: top 10px right -56px;
		opacity: 1;
	}

	@media (min-width: 470px) {
		background-position: top 10px right -10px;
	}

	@media (min-width: 768px) {
		background-position: top 20px right 45px;
		background-size: 355px;
	}

	@media (min-width: 992px) {
		background-position: top 20px right 45px;
		background-size: 450px;
	}

	@media (min-width: 1200px) {
		background-position: top 20px right 45px;
		background-size: 480px;
	}

	@media (min-width: 1400px) {
		background-position: top 20px right 45px;
		background-size: 580px;
	}
}

.main-hero__form {
	max-width: 320px;

	@media (min-width: 768px) {
		display: flex;
		flex-wrap: wrap;
		gap: 24px;
		max-width: unset;
		margin-top: 0;
	}

	@media (min-width: 992px) {
		align-items: flex-start;
	}

	.btn {
		@media (max-width: 767px) {
			width: 100%;
		}
	}

	.form-label {
		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}

	& > * {
		@media (min-width: 768px) {
			width: calc(50% - 12px);
		}

		&:nth-child(1) {
			@media (min-width: 992px) {
				width: calc(36% - 24px);
			}

			@media (min-width: 1200px) {
				width: calc(40% - 24px);
			}

			@media (min-width: 1400px) {
				width: 355px;
			}
		}

		&:nth-child(2) {
			@media (min-width: 992px) {
				width: calc(30% - 24px);
			}

			@media (min-width: 1200px) {
				width: calc(32% - 24px);
			}

			@media (min-width: 1400px) {
				width: 240px;
			}
		}

		&:nth-child(3) {
			@media (min-width: 992px) {
				width: calc(35% - 24px);
			}

			@media (min-width: 1200px) {
				width: calc(28% - 0px);
			}

			@media (min-width: 1400px) {
				width: 236px;
			}
		}
	}

	.form-text {
		font-size: 14px;
		line-height: 150%;

		@media (min-width: 768px) and (max-width: 991px) {
			padding-left: 18px;
		}

		@media (min-width: 992px) {
			text-align: center;
		}

		@media (min-width: 1200px) {
			text-align: left;
			font-size: 16px;
			margin-top: 12px;
		}
	}

	.btn {
		@media (min-width: 768px) and (max-width: 991px) {
			margin-left: auto;
			position: relative;
			top: -40px;
		}
	}
}